<template>
  <div class="diary">
    <p class="title">Expense</p>
    <hr />
    <div class="input-group w-25">
      <div class="input-group-append">
        <span class="input-group-text">収入を入力</span>
      </div>
      <input type="tel" class="form-control" v-model="income" />
    </div>
    <div class="input-group w-25 mt-3">
      <input type="file" @change="upFile" class="form-control" />
    </div>

    <dl class="mt-4" v-show="isShow">
      <div class="item">
        <dt class="mb-2">食費</dt>
        <dd>{{ food.toLocaleString() }}</dd>
      </div>
      <div class="item">
        <dt class="mb-2">電気</dt>
        <dd>{{ electric.toLocaleString() }}</dd>
      </div>
      <div class="item">
        <dt class="mb-2">ネット</dt>
        <dd>{{ net.toLocaleString() }}</dd>
      </div>
      <div class="item">
        <dt class="mb-2">ガス</dt>
        <dd>{{ gas.toLocaleString() }}</dd>
      </div>
      <div class="item">
        <dt class="mb-2">サブスク</dt>
        <dd>{{ subsc.toLocaleString() }}</dd>
      </div>
      <div class="item">
        <dt class="mb-2">Amazon</dt>
        <dd>{{ amazon.toLocaleString() }}</dd>
      </div>
      <div class="item">
        <dt class="mb-2">その他</dt>
        <dd>{{ other.toLocaleString() }}</dd>
        <div class="other-str">{{ otherStr }}</div>
      </div>

      <div class="total mt-5">
        <div class="input-group w-25 mt-3">
          <div class="input-group-append">
            <span class="input-group-text">収入</span>
          </div>
          <input type="text" class="form-control" v-model="income" />
        </div>
        <div class="input-group w-25 mt-3">
          <div class="input-group-append">
            <span class="input-group-text">支出</span>
          </div>
          <input type="text" class="form-control" v-model="expense" />
        </div>
        <br />
        <div class="item">
          <dt class="mb-2">貯蓄</dt>
          <dd>{{ deposit.toLocaleString() }}</dd>
        </div>
        <div class="item">
          <dt class="mb-2">貯蓄率</dt>
          <dd>{{ depositRate }}%</dd>
        </div>
      </div>
    </dl>
  </div>
</template>

<script>
export default {
  name: "Expense",
  data() {
    return {
      income: "",
      expense: 0,
      isShow: false,
      food: 0,
      electric: 0,
      net: 0,
      gas: 0,
      subsc: 0,
      amazon: 0,
      other: 0,
      otherStr: "",
    };
  },
  computed: {
    deposit() {
      return this.income - this.expense;
    },
    depositRate() {
      return Math.round(
        ((this.income - this.expense + 13000) / this.income) * 100
      );
    },
  },
  methods: {
    upFile(e) {
      let reader = new FileReader();
      reader.onload = () => {
        this.isShow = true;
        const cols = reader.result.split("\n");
        let tmpArr = [];
        for (const col of cols) {
          tmpArr.push(col.replace(new RegExp('"', "g"), "").split(","));
        }
        tmpArr.pop();
        tmpArr.splice(0, 1);
        const detail = tmpArr.filter((row) => {
          row.splice(0, 1);
          row.splice(2, 1);
          for (let i = 3; i < 10; i++) {
            row.splice(3, 1);
          }
          return row;
        });

        const TIT = 1;
        const VAL = 2;
        const foodReg =
          /ﾍﾟｲﾍﾟｲ|ｻﾝｴｰ|ﾏｸﾄﾞﾅﾙﾄﾞ|ﾘｳﾎﾞｳ|ｴｲｱﾝﾄﾞ|ﾊﾞﾂｸｽ|ｶﾈﾋﾃﾞ|ｺ-ﾋ-ﾀｲﾑ|UBER|ｲｵﾝ|ﾏﾂｸｽﾊﾞﾘﾕ-|ﾓｽﾊﾞ-ｶﾞ-|ｺﾒﾀﾞｺｰﾋｰ|ｶﾂﾃﾞﾘ|ﾄﾞﾗﾂｸﾞｽﾄｱﾓﾘ|ｼﾞﾖｲﾌﾙ|ﾗｰﾒﾝ|ｵｳｼﾖｳ|ﾌｱﾐﾘ-ﾏ-ﾄ|ｶﾞｽﾄ|ｷﾔﾂｽﾙﾅｶﾞﾀﾃ/;
        const subscReg = /ﾗｸﾃﾝﾓﾊﾞｲﾙ|ﾈﾂﾄﾌﾘﾂｸｽﾄﾞﾂﾄｺﾑ|ｱﾏｿﾞﾝﾌﾟﾗｲﾑｶｲﾋ|ｴﾈｵｽ-ｴｽｴｽ/;

        let otherStr = "　";
        let expense = 0;

        for (const row of detail) {
          const tit = row[TIT];
          expense += parseInt(row[VAL]);

          if (tit.match(foodReg)) {
            this.food += parseInt(row[VAL]);
          } else if (tit.match(/ｵｷﾅﾜﾃﾞﾝﾘﾖｸ/)) {
            this.electric += parseInt(row[VAL]);
          } else if (tit.match(/ﾋﾞﾂｸﾞﾛ-ﾌﾞ/)) {
            this.net += parseInt(row[VAL]);
          } else if (tit.match(/ﾏﾙｲｻﾝｷﾞﾖｳ ｶﾞｽ/)) {
            this.gas += parseInt(row[VAL]);
          } else if (tit.match(subscReg)) {
            this.subsc += parseInt(row[VAL]);
          } else if (tit.match(/AMAZON/)) {
            this.amazon += parseInt(row[VAL]);
          } else {
            this.other += parseInt(row[VAL]);
            otherStr += tit + "、";
          }
        }
        this.expense = expense;
        this.otherStr = otherStr.slice(0, -1);
      };
      reader.readAsText(e.target.files[0], "Shift_JIS");
    },
  },
};
</script>

<style scoped>
.title {
  font-size: 2rem;
  font-weight: 100;
}
.item {
  display: flex;
  flex-wrap: wrap;
}
.item dt,
.item dd {
  width: 80px;
}
.item dd {
  text-align: right;
}
.total > div {
  width: 180px !important;
}
</style>
